.App {
  text-align: center;
  color: #46566B;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.small-screen-text{
  font-size: 25px;
  padding: 20px;
  font-weight: bold;
  color: #46566B;
  ;
}
@media (max-width: 1200px) {
  .todo {
    display: none;
  }
}
@media (min-width: 1200px) {
  .small-screen-text{
    display: none;
  }
  .todo {
    display: block;
  }
}
